html {
  font-family: sans-serif;
  font-size: 125%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.618; }
  @media (min-width: 85.51777em) {
    html {
      font-size: 150%; } }
  html[data-os="Windows"] {
    font-family: Calibri, Meiryo, sans-serif; }
  html[data-os="Mac OS"], html[data-os="iOS"] {
    font-family: "PT Serif", serif; }

body {
  margin: 0;
  color: #242424;
  background-color: #fcfcfc; }

h1,
h2,
h3 {
  margin: 0 0 1.618rem; }

p {
  margin: 0 0 1.618rem; }

ol,
ul {
  margin: 0 0 1.618rem;
  padding-left: 0;
  list-style: none; }

figure {
  margin: 0 0 1.618rem; }

img {
  max-width: 100%; }

a {
  color: inherit;
  text-decoration: none;
  border-bottom: 2px solid;
  outline: 0;
  transition: all .2s ease-out; }
  a:hover, a:active, a:focus {
    color: #fcfcfc;
    background-color: #242424;
    border-bottom-color: transparent; }

template {
  display: none; }

h1,
h2,
h3, .page-header p, .articles {
  font-feature-settings: "kern" 1, "liga" 1;
  text-rendering: optimizeLegibility; }

h1,
h2,
h3 {
  line-height: 1.333; }

.page-header {
  max-width: 51.776rem;
  margin-top: 2.61792rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem; }
  .page-header h1 {
    font-size: 2.11796rem;
    font-weight: inherit; }
    @media (min-width: 42.5em) {
      .page-header h1 {
        font-size: 2.61792rem; } }
  .page-header h1 ruby {
    display: inline-flex;
    flex-direction: column-reverse; }
  .page-header h1 ruby:not(:last-child) {
    margin-right: .5em; }
  .page-header h1 rt {
    display: flex;
    justify-content: space-around;
    font-size: 0.76394rem; }
    @media (min-width: 42.5em) {
      .page-header h1 rt {
        font-size: 1rem; } }
  .page-header p {
    color: #5a5a5a; }
    @media (min-width: 42.5em) {
      .page-header p {
        font-size: 1.309rem; } }
  .page-header p a:hover,
  .page-header p a:active,
  .page-header p a:focus {
    background-color: #5a5a5a; }

.author-photo {
  margin-top: 4.2358rem;
  margin-bottom: 0; }
  @media (min-width: 42.5em) {
    .author-photo {
      max-width: 51.776rem;
      margin-top: 6.85353rem;
      margin-right: auto;
      margin-left: auto;
      padding-right: 1rem;
      padding-left: 1rem; } }
  .author-photo figure {
    position: relative;
    overflow: hidden;
    margin-left: 2.61792rem; }
    @media (min-width: 42.5em) {
      .author-photo figure {
        float: right;
        width: 38.1971%;
        margin-top: 0.20595rem;
        margin-bottom: 0.309rem;
        margin-left: 1.618rem; } }
    @media (min-width: 71.26481em) {
      .author-photo figure {
        margin-left: 2.61792rem; } }
  .author-photo figure::before {
    content: "";
    display: block;
    padding-top: 61.8047%; }
    @media (min-width: 42.5em) {
      .author-photo figure::before {
        padding-top: 75.01875%; } }
  .author-photo img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
  .author-photo img[src="/assets/author.jpg"] {
    transform: translate(-50%, -60%); }
    @media (min-width: 42.5em) {
      .author-photo img[src="/assets/author.jpg"] {
        transform: translate(-50%, -58%); } }
  .author-photo_overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column; }
  .author-photo_overlay::before, .author-photo_overlay::after {
    content: "";
    background-color: #fcfcfc; }
  .author-photo_overlay::before {
    flex-grow: 1;
    transform-origin: left; }
  .author-photo_overlay::after {
    flex-grow: 1.618;
    transform-origin: right; }
    @media (min-width: 42.5em) {
      .author-photo_overlay::after {
        flex-grow: 1.333; } }
  .is-visible .author-photo_overlay::before,
  .is-visible .author-photo_overlay::after {
    animation-name: author-photo_slide-out;
    animation-timing-function: cubic-bezier(0.22, 0.61, 0.36, 1);
    animation-fill-mode: forwards; }
  .is-visible .author-photo_overlay::before {
    animation-duration: .75s; }
  .is-visible .author-photo_overlay::after {
    animation-duration: .8s;
    animation-delay: .3s; }

@keyframes author-photo_slide-out {
  from {
    transform: scaleX(1); }
  to {
    transform: scaleX(0); } }

.section {
  max-width: 32rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem; }
  .section h2 {
    margin-top: 4.2358rem;
    font-size: 1.618rem; }
    @media (min-width: 42.5em) {
      .section h2 {
        margin-top: 6.85353rem; } }
    @media (min-width: 71.26481em) {
      .section h2 {
        margin-left: -2.61792rem; } }
  .section h3 {
    margin-top: 2.61792rem;
    margin-bottom: 1rem;
    font-size: 1.309rem; }
  @media (min-width: 71.26481em) {
    .section h3::before {
      content: "-";
      display: inline-flex;
      justify-content: center;
      width: 2.61792rem;
      margin-left: -2.61792rem; } }
  .author-photo + .section h2 + p::first-letter {
    float: left;
    margin-top: 0.309rem;
    font-size: 4.236rem;
    line-height: 1; }
    [data-os="Mac OS"] .author-photo + .section h2 + p::first-letter,
    [data-os="iOS"] .author-photo + .section h2 + p::first-letter {
      margin-right: -.05em;
      margin-left: -.175em; }

.articles-holder {
  min-height: 16.74248rem;
  margin-top: 1.618rem; }
  @media (min-width: 42.5em) {
    .articles-holder {
      min-height: 10.56219rem; } }

.articles li {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: 0.61805rem; }
  @media (min-width: 42.5em) {
    .articles li {
      flex-direction: row;
      align-items: center; } }

.articles time {
  font-size: 0.76394rem;
  color: #5a5a5a; }
  @media (min-width: 42.5em) {
    .articles time {
      margin-left: 1rem; } }
  @media (min-width: 71.26481em) {
    .articles time {
      width: 7.27008rem;
      margin-left: 2.61792rem;
      margin-right: -9.888rem; } }

.articles_title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; }
  @media (min-width: 42.5em) {
    .articles_title {
      flex-grow: 1;
      flex-basis: 0; } }

.articles_title a {
  border-bottom-width: 1px; }

.page-footer {
  display: flex;
  justify-content: flex-end;
  max-width: 51.776rem;
  margin: 6.85353rem auto 2.61792rem;
  padding-right: 1rem;
  padding-left: 1rem; }
  .page-footer p {
    margin-bottom: 0;
    border-top: 4px solid; }
